
import Search from 'vux/src/components/search/index.vue'
import Group from 'vux/src/components/group/index.vue'

import { mapState, mapActions, mapMutations } from 'vuex'
export default {
name: 'cityList',
data () {
return{
allCityList: [],
hotCitys: ["北京", "上海", "广州", "天津", "深圳", "重庆", "长沙", "长春", "成都", "福州", "南宁", "贵阳", "呼和浩特", "哈尔滨", "杭州", "合肥"],
results: [],
value: '',
appId: '79340',
sign: '9e151ce583aa4403bd3d3df0d894374e',
param: {

}
}
},
computed: {
...mapState('CITY', [
'cityList'
])
},
components: {
Search,
Group,
},
mounted() {
this.param = {}
this.param.showapi_appid = this.appId;
this.param.showapi_sign = this.sign;
this.$store.dispatch('CITY/getCityList',this.param).then(res => {
this.allCityList = res.data.showapi_res_body.data
})
},
methods: {
chooseCity(val) {
console.log(val)
if(this.$route.query.type == 'start') {
this.setOptions({
origin: val
})
}else{
this.setOptions({
destination: val
})
}
this.$router.push({name: 'index'})
},
...mapMutations('CONTAINER', [
'setOptions'
]),
...mapActions('CITY', [
'getCityList'
]),
setFocus () {
this.$refs.search.setFocus()
},
resultClick (item) {
if(this.$route.query.type == 'start') {
this.setOptions({
origin: item.title
})
}else{
this.setOptions({
destination: item.title
})
}
this.$router.push({name: 'index'})
},
getResult (val) {
console.log('on-change', val)
this.results = val ? getResult(this.value, this.allCityList) : []
},
onSubmit () {
this.$refs.search.setBlur()
this.$vux.toast.show({
type: 'text',
position: 'top',
text: 'on submit'
})
},
onFocus () {
console.log('on focus')
},
onCancel () {
console.log('on cancel')
}
}
}
function getResult (val,list) {
let rs = []
for (let i = 0; i < list.length; i++) {
if(list[i].cityName.search(val) != -1){
rs.push({
title: list[i].cityName,
other: list[i].pinYin
})
}
}

return rs
}



